.wishlist-list {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.wishlist-list h3 {
    margin-bottom: 20px;
}
.wishlist-list span {
    font-size: 1.1rem;
    font-weight: 500;
    margin-bottom: 10px;
}
.wishlist-list p {
    margin-left: 5px;
}

.wishlist-list hr {
    margin: 20px 40px;
    width: 60%;
    border: 1px solid var(--color-grey);
}

.wishlist .row {
    color: var(--color-text);
    align-items: center;
}

.edit-wishlist {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 80px 20px 20px 20px;
    padding: 20px;
    border-radius: 10px;
    background-color: var(--color-bg-light);
    border: 1px solid var(--color-grey);
}
.edit-wishlist:hover {
    cursor: pointer;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
}
.edit-wishlist span {
    margin-left: 20px;
    margin-bottom: 0px;
}
